html{background-color:#3400cd}html header{width:100%;display:flex;justify-content:space-around}html header h1.title{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;font-size:30px;margin:10px;color:#ffdc93;cursor:pointer}html header ul{display:flex;justify-content:space-between;list-style:none;z-index:100;margin:10px}html header ul a{color:white}html header ul a:visited{color:lightgray}html header ul a img{width:40px;height:40px;margin:5px}html header ul a img:hover{margin:3px;width:44px;height:44px}html footer{color:#ffdc93;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:20px auto}html footer a{text-decoration:underline}html footer a:hover{font-weight:bold}body{width:100vw;height:100vh}body section.game{position:relative;width:1000px;margin:0 auto;min-height:600px;max-width:100vw}body section.game section.splash{display:flex;flex-wrap:wrap;justify-content:space-evenly;z-index:1;opacity:1;position:absolute;top:30px;left:0;right:0;margin:auto;background-color:#ffdc93;padding:20px;border-radius:10px;border:2px solid #3400cd;box-shadow:0 6px 100px 0 rgba(0,0,0,0.8),0 100px 100px 0 rgba(0,0,0,0.19);max-width:790px}body section.game section.splash.hide{z-index:0;opacity:0;display:none}body section.game section.splash section.high-scores{padding:20px;border-radius:10px;border:2px solid #3400cd;font-family:Arial, Helvetica, sans-serif;color:#3400cd;max-width:100%;min-width:200px}body section.game section.splash section.high-scores h3{font-weight:bold;font-size:25px;text-align:center;margin-bottom:20px}body section.game section.splash section.high-scores article{display:flex;justify-content:space-between;border-bottom:1px dashed #3400cd}body section.game section.splash section.high-scores article p{line-height:25px}body section.game section.splash section.high-scores article:nth-child(2){border-top:1px dashed #3400cd}body section.game section.splash section.instructions{max-width:500px;font-size:20px;color:#3400cd;font-family:Arial, Helvetica, sans-serif}body section.game section.splash section.instructions button.start-button{height:40px;min-width:100px;background-color:#3400cd;color:white;margin:20px auto;font-size:20px;border-radius:4px;box-sizing:border-box;border:3px solid white;display:block}body section.game section.splash section.instructions button.start-button:hover{border:0;cursor:pointer}body section.game section.splash section.instructions p{text-align:center;line-height:30px}body section.game section.splash section.instructions section.items{display:flex;flex-wrap:wrap}body section.game section.splash section.instructions section.items article{padding:20px;border-radius:10px;border:2px solid #3400cd;margin:10px;flex-grow:1}body section.game section.splash section.instructions section.items article h3{font-weight:bold;color:#3400cd;font-family:Arial, Helvetica, sans-serif;font-size:25px;text-align:center}body section.game section.splash section.instructions section.items article ul{list-style:none;display:flex;justify-content:space-evenly}body section.game section.splash section.instructions section.items article ul li img{width:50px}body section.game section.splash section.instructions section.name-input{padding:20px;border-radius:10px;border:2px solid #3400cd;display:flex;margin-bottom:10px}body section.game section.splash section.instructions section.name-input label{margin:0 auto}body section.game section.splash section.instructions section.name-input label input{height:25px;background-color:transparent;border:0;font-size:20px;border-bottom:2px solid #3400cd}body section.game section.splash section.instructions section.name-input button{height:30px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;background-color:#3400cd;color:white;font-size:20px;border-radius:4px;box-sizing:border-box;border:3px solid white}body section.game canvas{position:absolute;max-width:100vw;border:2px solid white;box-sizing:border-box}
